#app4 {
}

@keyframes change{
    0%{
        background: red;
    }
    100%{
background: blue;
    }
}
#app4 .circle {
  border: red 1px solid;
  width: 10vw;
  height: 10vw;
  border-radius: 50% ;
  
}
#app4 .circle.active{
    animation: infinite change 1s alternate linear;
    
}